@import '../../styles/utils/palette';
@import '../../styles/utils/typography';
@import '../../styles/utils/mixins';

.navstring {
  display: flex;
  align-items: center;

  &__item {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    color: $white;

    &:not(:last-child) {
      margin-right: 30px;

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: -17px;
        width: 5px;
        height: 10px;
        background-color: red;
        background: url("data:image/svg+xml,%3Csvg width='6' height='10' viewBox='0 0 6 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.47146 0.528636C5.21111 0.268287 4.789 0.268287 4.52865 0.528636L0.528646 4.52864C0.268297 4.78899 0.268297 5.2111 0.528646 5.47145L4.52865 9.47145C4.789 9.7318 5.21111 9.7318 5.47146 9.47145C5.7318 9.2111 5.7318 8.78899 5.47146 8.52864L1.94286 5.00004L5.47146 1.47145C5.7318 1.2111 5.7318 0.788986 5.47146 0.528636Z' fill='%23B4BDC4'/%3E%3C/svg%3E%0A")
          center/contain no-repeat;
        transform: translate(0, -50%) rotate(180deg);
      }
    }

    &:not(:first-child) {
      margin-bottom: -1px;
    }

    &:last-child {
      color: $secondary;
      pointer-events: none;
    }
  }

  &__link {
    @extend %smallText_desktop-tablet;

    display: flex;
    align-items: center;
    justify-content: center;

    font-weight: 400;
  }

  &__home {
    width: 14px;
    height: 14px;

    margin-bottom: 0px;

    object-fit: contain;
  }

  &__arrow {
    display: inline-block;

    width: 6px;
    height: 10px;

    margin: 0 14px;

    transform: rotate(180deg);
  }
}

.link-reset {
  outline: 0;
  color: inherit;

  text-decoration: none;
  &:visited {
    text-decoration: none;
  }
}

.list-reset {
  margin: 0;
  padding: 0;

  text-indent: 0;
}